 little bit, maybe a few feet, where the effect was what they wanted, and when they reshot the scene, she could play it without the least inhibition. Just those few feet of film had inspired her. Later, Falconetti became able to play scenes only from Dreyers explanations, without the need even for rehearsal.\nSubsequent career\nAfter filming Joan of Arc, Falconetti continued with her career as a producer of light stage comedies, appearing with the Comdie-Franaise. During the Second World War, she escaped from France first to Switzerland, and then left for Buenos Aires, Argentina. Falconetti had suffered from mental illness all her life, and in 1946 she died in Brazil, allegedly due to a self-imposed restrictive diet after having become significantly overweight. Her remains are interred at Montmartre Cemetery in Paris. Her grandson Grard Falconetti also became an actor.")])])])
