<p><b>Gladys Louise Smith</b> (April 8, 1892  May 29, 1979), known professionally as Mary Pickford, was a Canadian-American film actress, writer, director, and producer. She was a co-founder of the film studio United Artists and one of the original 36 founders of the Academy of Motion Picture Arts and Sciences.
Known in her prime as Americas Sweetheart and the girl with the curls, Pickford was one of the Canadian pioneers in early Hollywood and a significant figure in the development of film acting. Pickford was one of the earliest stars to be billed under her name (film performers up until that time were usually unbilled), and was one of the most popular actresses of the 1910s and 20s, earning the nickname Queen of the Movies.
She was awarded the second ever Academy Award for Best Actress for her first sound film role in Coquette (1929) and also received an honorary Academy Award in 1976. In consideration of her contributions to American cinema, the American Film Institute ranked Pickford as 24th in its 1999 list of greatest female stars of classic Hollywood Cinema.</p>
<p><b>Early life</b><br>
Mary Pickford was born Gladys Louise Smith in 1892 (although she would later claim 1893 or 1894 as her year of birth) at 211 University Avenue,A Toronto, Ontario, Canada. Her father, John Charles Smith, was the son of English Methodist immigrants, and worked a variety of odd jobs. Her mother, Charlotte Hennessey, was of Irish Catholic descent and worked for a time as a seamstress. She had two younger siblings, Charlotte, called Lottie (born 1893), and John Charles, called Jack (born 1896), who also became actors. To please her husbands relatives, Pickfords mother baptized her children as Methodists, the faith of their father. John Charles Smith was an alcoholic; he abandoned the family and died on February 11, 1898, from a fatal blood clot caused by a workplace accident when he was a purser with Niagara Steamship.
When Gladys was age four, her household was under infectious quarantine, a public health measure. Their devoutly Catholic maternal grandmother (Catherine Faeley Hennessey) asked a visiting Roman Catholic priest to baptize the children. Pickford was at this time baptized as Gladys Marie Smith.
Charlotte Smith began taking in boarders after being widowed. One of these was a theatrical stage manager. At his suggestion, Gladys (age 7) was given two small roles, one as a boy and the other as a girl, in a stock company production of The Silver King at Torontos Princess Theatre. She subsequently acted in many melodramas with Torontos Valentine Company, finally playing the major child role in their version of The Silver King. She capped her short career in Toronto with the starring role of Little Eva in their production of Uncle Toms Cabin, adapted from the 1852 novel by American writer and abolitionist Harriet Beecher Stowe.</p>
<p><b>Career</b><br>
By the early 1900s, theatre had become a family enterprise. Gladys, her mother and two younger siblings toured the United States by rail, performing in third-rate companies and plays. After six impoverished years, Pickford allowed one more summer to land a leading role on Broadway, planning to quit acting if she failed. In 1906 Gladys, Lottie and Jack Smith supported singer Chauncey Olcott on Broadway in Edmund Burke. Gladys finally landed a supporting role in a 1907 Broadway play, The Warrens of Virginia. The play was written by William C. deMille, whose brother, Cecil, appeared in the cast. David Belasco, the producer of the play, insisted that Gladys Smith assume the stage name Mary Pickford. After completing the Broadway run and touring the play, however, Pickford was again out of work.
On April 19, 1909, the Biograph Company director D. W. Griffith screen-tested her at the companys New York studio for a role in the nickelodeon film, Pippa Passes. The role went to someone else but Griffith was immediately taken with Pickford. She quickly grasped that movie acting was simpler than the stylized stage acting of the day. Most Biograph actors earned $5 a day but, after Pickfords single day in the studio, Griffith agreed to pay her $10 a day against a guarantee of $40 a week.
Pickford, like all actors at Biograph, played both bit parts and leading roles, including mothers, ingenues, charwomen, spitfires, slaves, Native Americans, spurned women, and a prostitute. As Pickford said of her success at Biograph: I played scrubwomen and secretaries and women of all nationalities ... I decided that if I could get into as many pictures as possible, Id become known, and there would be a demand for my work.
She appeared in 51 films in 1909  almost one a week. She also introduced her friend Florence La Badie to D. W. Griffith, who launched La Badies career.
In January 1910, Pickford traveled with a Biograph crew to Los Angeles. Many other film companies wintered on the West Coast, escaping the weak light and short days that hampered winter shooting in the East. Pickford added to her 1909 Biographs (Sweet and Twenty, They Would Elope, and To Save Her Soul, to name a few) with films made in California.
Actors were not listed in the credits in Griffiths company. Audiences noticed and identified Pickford within weeks of her first film appearance. Exhibitors in turn capitalized on her popularity by advertising on sandwich boards that a film featuring The Girl with the Golden Curls, Blondilocks, or The Biograph Girl was inside.
Pickford left Biograph in December 1910. The following year, she starred in films at Carl Laemmles Independent Moving Pictures Company (IMP). IMP was absorbed into Universal Pictures in 1912, along with Majestic. Unhappy with their creative standards, Pickford returned to work with Griffith in 1912. Some of her best performances were in his films, such as Friends, The Mender of Nets, Just Like a Woman, and The Female of the Species. That year Pickford also introduced Dorothy and Lillian Gish (both friends from her days in touring melodrama) to Griffith. Both became major silent stars, in comedy and tragedy, respectively. Pickford made her last Biograph picture, The New York Hat, in late 1912.
She returned to Broadway in the David Belasco production of A Good Little Devil (1912). This was a major turning point in her career. Pickford, who had always hoped to conquer the Broadway stage, discovered how deeply she missed film acting. In 1913, she decided to work exclusively in film. The previous year, Adolph Zukor had formed Famous Players in Famous Plays. It was later known as Famous Players-Lasky and then Paramount Pictures, one of the first American feature film companies.
Pickford left the stage to join Zukors roster of stars. Zukor believed films potential lay in recording theatrical players in replicas of their most famous stage roles and productions. Zukor first filmed Pickford in a silent version of A Good Little Devil. The film, produced in 1913, showed the plays Broadway actors reciting every line of dialogue, resulting in a stiff film that Pickford later called one of the worst [features] I ever made ... it was deadly. Zukor agreed; he held the film back from distribution for a year.
Pickfords work in material written for the camera by that time had attracted a strong following. Comedy-dramas, such as In the Bishops Carriage (1913), Caprice (1913), and especially Hearts Adrift (1914), made her irresistible to moviegoers. Hearts Adrift was so popular that Pickford asked for the first of her many publicized pay raises based on the profits and reviews. The film marked the first time Pickfords name was featured above the title on movie marquees. Tess of the Storm Country was released five weeks later. Biographer Kevin Brownlow observed that the film sent her career into orbit and made her the most popular actress in America, if not the world.
Her appeal was summed up two years later by the February 1916 issue of Photoplay as luminous tenderness in a steel band of gutter ferocity. Only Charlie Chaplin, who reportedly slightly surpassed Pickfords popularity in 1916, had a similarly spellbinding pull with critics and the audience. Each enjoyed a level of fame far exceeding that of other actors. Throughout the 1910s and 1920s, Pickford was believed to be the most famous woman in the world, or, as a silent-film journalist described her, the best known woman who has ever lived, the woman who was known to more people and loved by more people than any other woman that has been in all history.</p>
<p><b>Stardom</b><br>
Pickford starred in 52 features throughout her career. On June 24, 1916, Pickford signed a new contract with Zukor that granted her full authority over production of the films in which she starred, and a record-breaking salary of $10,000 a week. In addition, Pickfords compensation was half of a films profits, with a guarantee of $1,040,000 (US$ 17,150,000 in 2016). Occasionally, she played a child, in films such as The Poor Little Rich Girl (1917), Rebecca of Sunnybrook Farm (1917), Daddy-Long-Legs (1919) and Pollyanna (1920). Pickfords fans were devoted to these Little Girl roles, but they were not typical of her career.
In August 1918, Pickfords contract expired and, when refusing Zukors terms for a renewal, she was offered $250,000 to leave the motion picture business. She declined, and went to First National Pictures, which agreed to her terms. In 1919, Pickford, along with D.W. Griffith, Charlie Chaplin, and Douglas Fairbanks, formed the independent film production company United Artists. Through United Artists, Pickford continued to produce and perform in her own movies; she could also distribute them as she chose. In 1920, Pickfords film Pollyanna grossed around $1,100,000. The following year, Pickfords film Little Lord Fauntleroy was also a success, and in 1923, Rosita grossed over $1,000,000 as well. During this period, she also made Little Annie Rooney (1925), another film in which Pickford played a child, Sparrows (1926), which blended the Dickensian with newly minted German expressionist style, and My Best Girl (1927), a romantic comedy featuring her future husband Buddy Rogers.
The arrival of sound was her undoing. Pickford underestimated the value of adding sound to movies, claiming that adding sound to movies would be like putting lipstick on the Venus de Milo.
She played a reckless socialite in Coquette (1929), a role for which her famous ringlets were cut into a 1920s bob. Pickford had already cut her hair in the wake of her mothers death in 1928. Fans were shocked at the transformation. Pickfords hair had become a symbol of female virtue, and when she cut it, the act made front-page news in The New York Times and other papers. Coquette was a success and won her an Academy Award for Best Actress, although this was highly controversial. The public failed to respond to her in the more sophisticated roles. Like most movie stars of the silent era, Pickford found her career fading as talkies became more popular among audiences.
Her next film, The Taming of The Shrew, made with husband Douglas Fairbanks, was not well received at the box office. Established Hollywood actors were panicked by the impending arrival of the talkies. On March 29, 1928, The Dodge Brothers Hour was broadcast from Pickfords bungalow, featuring Fairbanks, Chaplin, Norma Talmadge, Gloria Swanson, John Barrymore, D.W. Griffith, and Dolores del Rio, among others. They spoke on the radio show to prove that they could meet the challenge of talking movies.
But the transition came as Pickford was in her late 30s, no longer able to play the children, teenage spitfires, and feisty young women so adored by her fans, and was not suited for the glamorous and vampish heroines of early sound. In 1933, Pickford underwent a Technicolor screen test for an animated/live action film version of Alice in Wonderland, but Walt Disney discarded the project when Paramount released its own version of the book. Only one Technicolor still of her screen test still exists. She retired from acting in 1933; her last acting film was released in 1934. She continued to produce for others, however, including Sleep, My Love (1948; with Claudette Colbert) and Love Happy (1949), with the Marx Brothers).</p>
<p><b>The film industry</b><br>
Mary Pickford giving President Herbert Hoover a ticket for a film industry benefit for the unemployed, 1931
Pickford used her stature in the movie industry to promote a variety of causes. Although her image depicted fragility and innocence, Pickford proved to be a worthy businesswoman who took control of her career in a cutthroat industry.
During World War I, she promoted the sale of Liberty Bonds, making an intensive series of fund-raising speeches that kicked off in Washington, D.C., where she sold bonds alongside Charles Chaplin, Douglas Fairbanks, Theda Bara, and Marie Dressler. Five days later she spoke on Wall Street to an estimated 50,000 people. Though Canadian-born, she was a powerful symbol of Americana, kissing the American flag for cameras and auctioning one of her world-famous curls for $15,000. In a single speech in Chicago she sold an estimated five million dollars worth of bonds. She was christened the U.S. Navys official Little Sister; the Army named two cannons after her and made her an honorary colonel.
At the end of World War I, Pickford conceived of the Motion Picture Relief Fund, an organization to help financially needy actors. Leftover funds from her work selling Liberty Bonds were put toward its creation, and in 1921, the Motion Picture Relief Fund (MPRF) was officially incorporated, with Joseph Schenck voted its first president and Pickford its vice president. In 1932, Pickford spearheaded the Payroll Pledge Program, a payroll-deduction plan for studio workers who gave one half of one percent of their earnings to the MPRF. As a result, in 1940, the Fund was able to purchase land and build the Motion Picture Country House and Hospital, in Woodland Hills, California.
An astute businesswoman, Pickford became her own producer within three years of her start in features. According to her Foundation, she oversaw every aspect of the making of her films, from hiring talent and crew to overseeing the script, the shooting, the editing, to the final release and promotion of each project. She demanded (and received) these powers in 1916, when she was under contract to Zukors Famous Players In Famous Plays (later Paramount). Zukor acquiesced to her refusal to participate in block-booking, the widespread practice of forcing an exhibitor to show a bad film of the studios choosing to also be able to show a Pickford film. In 1916, Pickfords films were distributed, singly, through a special distribution unit called Artcraft. The Mary Pickford Corporation was briefly Pickfords motion-picture production company.
In 1919, she increased her power by co-founding United Artists (UA) with Charlie Chaplin, D. W. Griffith, and her soon-to-be husband, Douglas Fairbanks. Before UAs creation, Hollywood studios were vertically integrated, not only producing films but forming chains of theaters. Distributors (also part of the studios) arranged for company productions to be shown in the companys movie venues. Filmmakers relied on the studios for bookings; in return they put up with what many considered creative interference.
United Artists broke from this tradition. It was solely a distribution company, offering independent film producers access to its own screens as well as the rental of temporarily unbooked cinemas owned by other companies. Pickford and Fairbanks produced and shot their films after 1920 at the jointly owned Pickford-Fairbanks studio on Santa Monica Boulevard. The producers who signed with UA were true independents, producing, creating and controlling their work to an unprecedented degree. As a co-founder, as well as the producer and star of her own films, Pickford became the most powerful woman who has ever worked in Hollywood. By 1930, Pickfords acting career had largely faded. After retiring three years later, however, she continued to produce films for United Artists. She and Chaplin remained partners in the company for decades. Chaplin left the company in 1955, and Pickford followed suit in 1956, selling her remaining shares for three million dollars.</p>
<p><b>Personal life</b><br>
Pickford was married three times. She married Owen Moore, an Irish-born silent film actor, on January 7, 1911. It is rumored she became pregnant by Moore in the early 1910s and had a miscarriage or an abortion. Some accounts suggest this resulted in her later inability to have children. The couple had numerous marital problems, notably Moores alcoholism, insecurity about living in the shadow of Pickfords fame, and bouts of domestic violence. The couple lived together on-and-off for several years.
Pickford became secretly involved in a relationship with Douglas Fairbanks. They toured the US together in 1918 to promote Liberty Bond sales for the World War I effort. Around this time, Pickford also suffered from the flu during the 1918 flu pandemic, but survived. Pickford divorced Moore on March 2, 1920, after she agreed to his $100,000 demand for a settlement. She married Fairbanks just days later on March 28, 1920. They went to Europe for their honeymoon; fans in London and in Paris caused riots trying to get to the famous couple. The couples triumphant return to Hollywood was witnessed by vast crowds who turned out to hail them at railway stations across the United States.
The Mark of Zorro (1920) and a series of other swashbucklers gave the popular Fairbanks a more romantic, heroic image. Pickford continued to epitomize the virtuous but fiery girl next door. Even at private parties, people instinctively stood up when Pickford entered a room; she and her husband were often referred to as Hollywood royalty. Their international reputations were broad. Foreign heads of state and dignitaries who visited the White House often asked if they could also visit Pickfair, the couples mansion in Beverly Hills.
Dinners at Pickfair included a number of notable guests. Charlie Chaplin, Fairbanks best friend, was often present. Other guests included George Bernard Shaw, Albert Einstein, Elinor Glyn, Helen Keller, H. G. Wells, Lord Mountbatten, Fritz Kreisler, Amelia Earhart, F. Scott Fitzgerald, Nol Coward, Max Reinhardt, Baron Nishi, Vladimir Nemirovich-Danchenko, Sir Arthur Conan Doyle, Austen Chamberlain, Sir Harry Lauder, and Meher Baba, among others. The public nature of Pickfords second marriage strained it to the breaking point. Both she and Fairbanks had little time off from producing and acting in their films. They were also constantly on display as Americas unofficial ambassadors to the world, leading parades, cutting ribbons, and making speeches. When their film careers both began to flounder at the end of the silent era, Fairbanks restless nature prompted him to overseas travel (something which Pickford did not enjoy). When Fairbanks romance with Sylvia, Lady Ashley became public in the early 1930s, he and Pickford separated. They divorced January 10, 1936. Fairbanks son by his first wife, Douglas Fairbanks Jr., claimed his father and Pickford long regretted their inability to reconcile.
On June 24, 1937, Pickford married her third and last husband, actor and band leader Buddy Rogers. They adopted two children: Roxanne (born 1944, adopted 1944) and Ronald Charles (born 1937, adopted 1943, a.k.a. Ronnie Pickford Rogers). As a PBS American Experience documentary noted, Pickfords relationship with her children was tense. She criticized their physical imperfections, including Ronnies small stature and Roxannes crooked teeth. Both children later said their mother was too self-absorbed to provide real maternal love. In 2003, Ronnie recalled that Things didnt work out that much, you know. But Ill never forget her. I think that she was a good woman.</p>
<p><b>Later years</b><br>
After retiring from the screen, Pickford became an alcoholic, as her father had been. Her mother Charlotte died of breast cancer in March 1928. Her siblings, Lottie and Jack, both died of alcohol-related causes. These deaths, her divorce from Fairbanks, and the end of silent films left Pickford deeply depressed. Her relationship with her children, Roxanne and Ronald, was turbulent at best. Pickford withdrew and gradually became a recluse, remaining almost entirely at Pickfair and allowing visits only from Lillian Gish, her stepson Douglas Fairbanks, Jr., and few other people. She appeared in court in 1959, in a matter pertaining to her co-ownership of North Carolina TV station WSJS-TV. The court date coincided with the date of her 67th birthday; under oath, when asked to give her age, Pickford replied, Im 21, going on 20.
In the mid-1960s, Pickford often received visitors only by telephone, speaking to them from her bedroom. Buddy Rogers often gave guests tours of Pickfair, including views of a genuine western bar Pickford had bought for Douglas Fairbanks, and a portrait of Pickford in the drawing room. A print of this image now hangs in the Library of Congress. In addition to her Oscar as best actress for Coquette (1929), Mary Pickford received an Academy Honorary Award in 1976 for lifetime achievement. The Academy sent a TV crew to her house to record her short statement of thanks  offering the public a very rare glimpse into Pickfair Manor.
Pickford had become an American citizen upon her marriage to Fairbanks in 1920. Toward the end of her life, Pickford made arrangements with the Department of Citizenship to regain her Canadian citizenship because she wished to die as a Canadian. Her request was approved and she became a dual Canadian-American citizen.</p>
<p><b>Death</b><br>
On May 29, 1979, Pickford died at a Santa Monica, California hospital of complications from a cerebral hemorrhage she had suffered the week before. She was interred in the Garden of Memory of the Forest Lawn Memorial Park cemetery in Glendale, California. Buried alongside her in the Pickford private family plot are her mother Charlotte, her siblings Lottie and Jack Pickford, and the family of Elizabeth Watson, Charlottes sister, who had helped raise Pickford in Toronto.</p>
