            <div class="paper__half">
<p><b>Mabel Normand</b> (November 9, 1892 February 23, 1930) was an American silent film actress, screenwriter, director, and producer. She was a popular star and collaborator of Mack Sennett in his Keystone Studios films and, at the height of her career in the late 1910s and early 1920s, had her own movie studio and production company. Onscreen, she appeared in 12 successful films with Charles Chaplin and 17 with Roscoe Fatty Arbuckle, sometimes writing and directing (or co-writing/directing) movies featuring Chaplin as her leading man. Throughout the 1920s, her name was linked with widely publicized scandals, including the 1922 murder of William Desmond Taylor and the 1924 shooting of Courtland S. Dines, who was shot by Normands chauffeur using her pistol. She was not a suspect in either crime. Her film career declined, possibly due to both scandals and a recurrence of tuberculosis in 1923, which led to a decline in her health, retirement from films, and her death in 1930 at age 37.</p>
<p><b>Early life and career height</b><br>
Born Mabel Ethelreid Normand in New Brighton, Staten Island, New York, she grew up in a working-class family. Normands mother was of Irish heritage, while her father was French Canadian. Her father Claude Normand was employed as a cabinet maker and stage carpenter at Sailors Snug Harbor home for elderly seamen. Before she entered films at age 16 in 1909, Normand worked as an artists model, which included posing for postcards illustrated by Charles Dana Gibson, creator of the Gibson Girl image, as well as for Buttericks clothing pattern manufacturers in lower Manhattan. For a short time, she worked for Vitagraph Studios in New York City for $25 per week, but Vitagraph founder Albert E. Smith admitted she was one of several actresses about whom he made a mistake in estimating their potential for future stardom. Her quietly effervescent lead performance, while directed by D. W. Griffith in the dramatic 1911 short film Her Awakening, drew attention and she met director Mack Sennett while at Griffiths Biograph Company. She embarked on a topsy-turvy relationship with him; he later brought her across to California when he founded Keystone Studios in 1912. Her earlier Keystone films portrayed her as a bathing beauty but Normand quickly demonstrated a flair for comedy and became a major star of Sennetts short films. Normand appeared with Charles Chaplin and Roscoe Fatty Arbuckle in many short films as well as Oliver Hardy, Stan Laurel, and Boris Karloff.
She played a key role in starting Chaplins film career and acted as his leading lady and mentor in a string of films in 1914, sometimes co-writing and directing or co-directing films with him. Chaplin had considerable initial difficulty adjusting to the demands of film acting and his performance suffered for it. After his first film appearance in Making a Living, Sennett felt he had made a costly mistake. Most historians agree it was Normand who persuaded him to give Chaplin another chance, and she and Chaplin appeared together in a dozen subsequent films, almost always as a couple in the lead roles. In 1914, she starred with Marie Dressler and Chaplin in Tillies Punctured Romance, the first feature-length comedy. Earlier that same year, in January/February, Chaplin first played his Tramp character in Mabels Strange Predicament, although it wound up being the second Tramp film released; Chaplin offers an account of his experience on the film in his autobiography.
She opened her own company in partnership with Mack Sennett 1916. It was based in Culver City and was a subsidiary of the Triangle Film Corperation. She lost the company in 1918 when Triangle experienced a massive shake up which also saw Sennett lose Keystone and establish his own independent studio.
In 1918, as her relationship with Sennett came to an end, Normand signed a $3,500 per week contract with Samuel Goldwyn.</p>
<p><b>Taylors murder</b><br>
Director William Desmond Taylor shared her interest in books, and the two formed a close relationship. According to author Robert Giroux, Taylor was deeply in love with Normand, who had originally approached him for help in curing her cocaine dependency. Based upon Normands subsequent statements to investigators, her repeated relapses were devastating for Taylor. According to Giroux, Taylor met with federal prosecutors shortly before his death and offered to assist them in filing charges against Normands cocaine suppliers. Giroux expresses a belief that Normands suppliers learned of this meeting and hired a contract killer to murder the director. According to Giroux, Normand suspected the reasons for Taylors murder, but did not know the identity of the man who killed him.According to Kevin Brownlow and John Kobal in their book Hollywood The Pioneers the idea that Taylor was murdered by drug dealers was invented by the studio for publicity purposes. There is no evidence that Normand was an addict, despite the fact that this is often repeated as if it were established fact.
On the night of his murder, February 1, 1922, Normand left Taylors bungalow at 7:45 p.m. in a happy mood, carrying a book he had lent her. They blew kisses to each other as her limousine drove away. Normand was the last person known to have seen Taylor alive. The Los Angeles Police Department subjected Normand to a grueling interrogation, but ruled her out as a suspect. Most subsequent writers have done the same. However, Normands career had already slowed, and her reputation was tarnished. According to George Hopkins, who sat next to her at Taylors funeral, Normand wept inconsolably.</p>
<p><b>The Dines shooting</b><br>
In 1924, Normands chauffeur Joe Kelly shot and wounded millionaire oil broker and amateur golfer Courtland S. Dines with her pistol. At the time of the shooting, Dines was romantically involved with Normands friend (and frequent Chaplin co-star) Edna Purviance. Purviance was also the next door neighbor of William Desmond Taylor.</p>
<p><b>The Roscoe Arbuckle trials</b><br>
Normands co-star in many films, Roscoe Arbuckle was the defendant in three widely publicized trials for the rape and manslaughter of actress Virginia Rappe. Although Arbuckle was acquitted, the scandal destroyed his career, and his films were banned from exhibition for decades. Since she had made some of her best works with him, much of Normands output was withheld from the public by default.</p>
<p><b>Later career and death</b><br>
Normand continued making films and was signed by Hal Roach Studios in 1926 after discussions with director/producer F. Richard Jones, who had directed her at Keystone. At Roach she made the films Raggedy Rose, The Nickel-Hopper, and One Hour Married (her last film), all co-written by Stan Laurel, and was directed by Leo McCarey in Should Men Walk Home?. The films were released with extensive publicity support from the Hollywood community, including her friend Mary Pickford.
In 1926, she married actor Lew Cody, with whom she had appeared in Mickey in 1918. They lived separately in nearby houses in Beverly Hills. However, Normands health was in decline due to tuberculosis. After an extended stay in Pottenger Sanitorium, she died on February 23, 1930 from tuberculosis in Monrovia, California at the age of 37. She was interred as Mabel Normand-Cody at Calvary Cemetery, Los Angeles.</p>
<p><b>Legacy</b><br>
Mabel Normand has a star on the Hollywood Walk of Fame for her contributions to Motion Pictures at 6821 Hollywood Boulevard.
Her film Mabels Blunder (1914) was added to the National Film Registry in December 2009.
In June 2010, the New Zealand Film Archive reported the discovery of a print of Normands film Won in a Closet (exhibited in New Zealand under its alternate title Won in a Cupboard), a short comedy previously believed lost. This film is a significant discovery, as Normand directed the movie and starred in the lead role, displaying her talents on both sides of the camera.</p>
<p><b>Cultural references</b><br>
A nod to Normands celebrity in early Hollywood came through the name of a leading character in the 1950 film Sunset Boulevard, Norma Desmond, which has been cited as a combination of the names Mabel Normand and William Desmond Taylor. The film also frequently mentions Normand by name.
The 1974 Broadway musical Mack & Mabel (Michael Stewart and Jerry Herman) fictionalized the romance between Normand and Mack Sennett. Normand was played by Bernadette Peters and Robert Preston portrayed Mack Sennett.
Hello Mabel is a song by the Bonzo Dog Doo-Dah Band released in England on their second album The Doughnut in Grannys Greenhouse (released as Urban Spaceman in the US.) in November 1968.
Normand is mentioned during Series 2 Episode 1 of Downton Abbey by ambitious housemaid Ethel Parks. Daisy Mason (ne Robinson), the kitchen maid, inquires what she is reading and Ethel responds, Photoplay about Mabel Normand. She was nothing when she started, you know. Her father was a carpenter and theyd no money, and now shes a shining film star.
Singer/songwriter Stevie Nicks wrote a song about the actress entitled Mabel Normand, which appears on her 2014 album, 24 Karat Gold: Songs from the Vault.</p>
<p><b>Fictional portrayals</b><br>
Normand is played by actress Marisa Tomei in the 1992 film Chaplin opposite Robert Downey, Jr. as Charles Chaplin; by Penelope Lagos in the first biopic about Normands life, a 35-minute dramatic short film entitled Madcap Mabel (2010); and by Morganne Picard in the motion picture Return to Babylon (2013).
In 2014, Normand was played on television by Andrea Deck in Series 2, Episode 8 of Mr Selfridge and by Kristina Thompson in the short film Mabels Dressing Room.</p>
