            <div v-if="$route.params.lang === 'en'">
<p><b>Kinetoscope</b><br>
The Kinetoscope is an early motion picture exhibition device. The Kinetoscope was designed for films to be viewed by one individual at a time through a peephole viewer window at the top of the device. The Kinetoscope was not a movie projector, but introduced the basic approach that would become the standard for all cinematic projection before the advent of video, by creating the illusion of movement by conveying a strip of perforated film bearing sequential images over a light source with a high-speed shutter. A process using roll film first described in a patent application submitted in France and the U.S. by French inventor Louis Le Prince, the concept was copied by U.S. inventor Thomas Edison in 1889, and subsequently developed by his employee William Kennedy Laurie Dickson between 1889 and 1892.[1] Dickson and his team at the Edison lab also devised the Kinetograph, an innovative motion picture camera with rapid intermittent, or stop-and-go, film movement, to photograph movies for in-house experiments and, eventually, commercial Kinetoscope presentations.
A prototype for the Kinetoscope was shown to a convention of the National Federation of Women's Clubs on May 20, 1891. The first public demonstration of the Kinetoscope was held at the Brooklyn Institute of Arts and Sciences on May 9, 1893. Instrumental to the birth of American movie culture, the Kinetoscope also had a major impact in Europe; its influence abroad was magnified by Edison's decision not to seek international patents on the device, facilitating numerous imitations of and improvements on the technology. In 1895, Edison introduced the Kinetophone, which joined the Kinetoscope with a cylinder phonograph. Film projection, which Edison initially disdained as financially nonviable, soon superseded the Kinetoscope's individual exhibition model. Many of the projection systems developed by Edison's firm in later years would use the Kinetoscope name.</p>
<p><b>Projecting Kinetoscopes</b><br>
Over the course of 1895, it became clear that the Kinetoscope was going to lose out on one end to projected motion pictures and, on the other, to a new "peep show" device, the cheap, flip-book-based Mutoscope. In its second year of commercialization, the Kinetoscope operation's profits plummeted by more than 95 percent, to just over $4,000. The Latham brothers and their father, Woodville, had retained the services of former Edison employee Eugene Lauste and then, in April 1895, Dickson himself to develop a film projection system. On May 20, in New York City, the new Eidoloscope was used for the first commercial screening of a motion picture: a boxing match between Young Griffo and Charles Barnett, four or eight minutes long. European inventors, most prominently the Lumires and Germany's Skladanowsky brothers, were moving forward with similar systems.
By the beginning of 1896, Edison had turned his attention to promoting a projector technology, the Phantoscope, developed by young inventors Charles Francis Jenkins and Thomas Armat. The rights to the system had been acquired by Raff and Gammon, who redubbed it the Vitascope and arranged with Edison to present himself as its creator.  With Dickson having left his employ, the Kinetophone was soon mothballed and Edison suspended work on sound cinema for an extended period. Departing the Vitascope operation after little more than a year, Edison commissioned the development of his own projection systems, the Projectoscope and then multiple iterations of the Projecting Kinetoscope. In 1912, he introduced the ambitious and expensive Home Projecting Kinetoscope, which employed a unique format of three parallel columns of sequential frames on one strip of filmthe middle column ran through the machine in the reverse direction from its neighbors. It was a commercial failure. Four years later, the Edison operation came out with its last substantial new film exhibition technology, a short-lived theatrical system called the Super Kinetoscope. Much of the Edison company's most creative work in the motion picture field from 1897 on involved the use of Kinetoscope-related patents in threatened or actual lawsuits for the purpose of financially pressuring or blocking commercial rivals. 
As far back as the Vitascope days, some exhibitors had screened films accompanied by phonographs playing appropriate, though very roughly timed, sound effects; in the style of the Kinetophone described above, rhythmically matching recordings were also made available for march and dance subjects. While Edison oversaw cursory sound-cinema experiments after the success of The Great Train Robbery (1903) and other Edison Manufacturing Company productions, it was not until 1908 that he returned in earnest to the combined audiovisual concept that had first led him to enter the motion picture field. Edison patented a synchronization system connecting a projector and a phonograph, located behind the screen, via an assembly of three rigid shaftsa vertical one descending from each device, joined by a third running horizontally the entire length of the theater, beneath the floor. Two years later, he supervised a press demonstration at the laboratory of a sound-film system of either this or a later design. In 1913, Edison finally introduced the new Kinetophonelike all of his sound-film exhibition systems since the first in the mid-1890s, it used a cylinder phonograph, now connected to a Projecting Kinetoscope via a fishing linetype belt and a series of metal pulleys. While it met with great acclaim in the short term, poorly trained operators had trouble keeping picture in synchronization with sound and, like other sound-film systems of the era, the Kinetophone had not solved the issues of insufficient amplification and unpleasant audio quality. Its drawing power as a novelty soon faded and when a fire at Edison's West Orange complex in December 1914 destroyed all of the company's Kinetophone image and sound masters, the system was abandoned.</p>
