  return _c('p', [_c('b', [_vm._v("History")]), _c('br'), _vm._v("\nIn 1876, Reynaud, aFrenchinventor, had created thePraxinoscope, an improvement of theZoetrope. The Praxinoscope replaced the narrow viewing slits of the Zoetrope with an inner circle of 12 mirrors (equal to the number of images), allowing a clearer and less distorted view of the moving image. Several people could watch the performance at the same time. After Reynaud licensed his invention in 1877, it sold well in a number of the largeParisdepartment stores.\nIn 1878, Reynaud produced thePraxinoscope Theatre. This improved version included a glass viewing screen which allowed the moving image to be superimposed over a changeable background. He continued to improve the design and in 1880 created the first projection version.\nIn 1888, he improved the projection version, which was a similar projectors desing which would be used for cinema projection a few years later. Glass plates, individually painted by Reynaud himself, were mounted in leather bands. Each of the bands were connected by a metal strip with a hole, which allowed it to locate on a pin on the rotating drum and align the image with the projecting lantern. Reynaud presented the patent of its Thtre Optique the December 1, 1888 and it differentiates of the optical toys ("), _vm._v("Zoetrope,PraxinoscopeorZoopraxiscope) because it was designed to obtain the illusion of movement with a great variety of animation scenes, which go on for a long time, and it was not limited to a continuous repetition of the same images. \nOn October 28, 1892 he gave his first public performance of a moving picture show at theMuse GrvininParis. The show, billed asPantomimes Lumineuses, included three cartoons,Pauvre Pierrot,Un bon bock, andLe Clown et ses chiens. Reynaud acted as the projectionist and the show was accompanied by a piano player. Although the films shown by the Lumire Brothers in 1895 eclipsed it, the show stayed at theMuse Grvinuntil 1900 and over 500,000 people had seen it.\nReynaud ended up ruined and forgotten. One of the reasons for his short fame was that the size of his film tapes were too big to make copies. In 1913 Reynaud throw himself to the Seine with his last copy of a Thtre Optique, although a few days later he would be visited by a French inventor and producerLon Gaumont, to buy the invention and donate it to theConservatoire des Arts et Mtiers.")])
