  return _c('p', [_c('b', [_vm._v("Pantomimes Lumineuses")]), _c('br'), _vm._v("\nThe first show that included moving images was performed at the Grvin museum in Paris in 1892, a few years before the Lumire brothers made their first projection. Reynaud's works can be considered animations and theirPantomimes Lumineusesthe first animated films. More than 12,800 representations, which attended more than half a million people, were made until it closed in 1900.\nThe performances could last one minute and a half or five minutes the longest, and they could be longer depending on the operator's movements with the coils.\nThe Grvin Museum signed a contract to represent the Pantomimes to Charles-mile Reynaud, who linked it to represent the show in two shifts with the frequency demanded by the public, from 15h to 18h and from 20h to 23h and forced to modify it to least once a year.")])
