  return _c('p', [_c('b', [_vm._v("Shows")]), _c('br'), _vm._v("\nThe first programme included three shows. The first one wasPauvre Pierrot!withGaston Paulinon the piano and a series of scores were created for the projections, so we can that these were the first soundtracks. The film had a lenght of 36 meters and there were 500 images in total, which in the public saw the dispute of the protagonist, Pierrot, with Arlequine by the love of Colombine, but this one ignored Pierrot and in the end he got the love of Arlequine. The story was so successful in its premiere in 1892 that it was a work projected until February 1892.\nThe second performance wasLeClownet ses chiens,of 22 meters and 300 images, where the protagonist, a clown, did a circus numbers with their dogs.\nFinally, the third wasUn bon bock, which had 50 meters lenght and 700 images, showed a rude man who entered a bar and watched his beer disappearing in front of him.\nLater, Charles-mile Reynaud created other shows like:Autour d'une cabine, which had 45 meters and 636 images, and it explained the story of a bather women molested by a shameless man in front of her husband; another well-known work isA rve au coin du feu, which had 29 meters and 400 images, where the use of a flashback was used for the first time as a narrative element to explain the past of the protagonist when his house was devoured by the flames.")])
