  return _c('p', [_c('b', [_vm._v("Operation")]), _c('br'), _vm._v("\nCharles-mile Reynaud used the combination of two magic lanterns to make his shows. The first one projected to the bottom of the scene and the second one that projected the painted images, by means of mirrors and diverse lenses, in the plates of the band of fabric pierced in two coils that moved manually.\nReynaud used a strip of cloth, with the sides of the tape reinforced with metallic bands and flexible, about 70mm with lacquer rubber plates protected with gelatin, making the whole process a very tedious and complicated work. The bands had a central perforation between each of the drawings in order to be able to drive the film through the drag. Although the mechanism was actuated by a cylinder of the mirror through a pegs.\nThe ribbon of the film was the flexible strip which was made of plates and placed inside the first coil, then the film was advancing in front of the magic lantern, guided by the central perforations in the ribbon until reaching the cylinder of 36 Rotating mirrors. The film was received by a second coil which the operator had to rotate simultaneously and passed about 15 images per second, depending on the speed of the operator. ThePHI phenomenonprolonged the image display. The light that came to the mirrors was led, through other mirrors, to the rear of the projection screen.\nThe figures of his representations were painted by hand on a photosensitive support with transparent inks with aniline, a technique that was already used for magic lantern shows. All the outline of the characters and their support was painted with black ink so that the light did not pass between the glass.\nOn the other hand, the decoration and the landscape behind the figures was designed and painted on another glass plate and projected with the other magic lantern.")])
