icence as \"too many film licences were already in number of films in the early twentieth century, some directed by Eugen, his younger brother, but with little success. In his later years Skladanowsky was accused in the press of exaggerating his role in the early days of cinema, most notably by the pioneering cameraman Guido Seeber.circulation\"\nAfter this Skladanowsky returned to his former photographic activities including the production of flip books and further magic lantern shows. He also sold amateur film cameras and projectors and produced 3-D anaglyph image slides. His company Projektion fr Alle also produced a number of films in the early 20th century, some directed by Eugen, his younger brother, but with little success. In his later years Skladanowsky was accused in the press of exaggerating his role in the early days of cinema, most notably by the pioneering cameraman Guido Seeber.")])
